@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Orbitron:wght@400;700&display=swap');

:root {
  --primary-bg-color: #35495e;
  --secondary-bg-color: #627c91;
  --primary-color: #ffffff;
  --secondary-color: #b0c4de;
  --highlight-color: #62A8AC;
  --border-radius: 10px;
  --box-shadow-color: rgba(50, 50, 93, 0.25);
}

html,
body {
  font-family: 'Montserrat', sans-serif;
  font-size: 100%;
  color: var(--primary-color);
  height: 100%;
  margin: 0;
  background: transparent;
  overflow-x: hidden;
  /* Hide overflow horizontally (left and right) */
  overflow-y: hidden;
}

@keyframes flickerAnimation {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.3;
  }

  100% {
    opacity: 1;
  }
}

@-o-keyframes flickerAnimation {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.3;
  }

  100% {
    opacity: 1;
  }
}

@-moz-keyframes flickerAnimation {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.3;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes flickerAnimation {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.3;
  }

  100% {
    opacity: 1;
  }
}

.flickerIt {
  -webkit-animation: flickerAnimation 2s infinite;
  -moz-animation: flickerAnimation 2s infinite;
  -o-animation: flickerAnimation 2s infinite;
  animation: flickerAnimation 2s infinite;
}



.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90vw;
  margin: auto;
  padding: 1.25rem;
}

.controls-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1140px;
  margin: auto;
  gap: 1rem;
}

.header,
.nav-buttons,
.search-container,
.paste-button {
  width: 100%;
  max-width: 1140px;
  margin-bottom: 1rem;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 0px;
}

.logo {
  height: 80px;
  width: auto;
  margin-bottom: 0px;
}

.header-line {
  height: 1.6px;
  background-color: white;
  width: 50%;
  margin: 3px 20px 20px 20px;
}

.nav-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.button-59 {
  background-color: transparent;
  border: 2px solid var(--secondary-color);
  color: var(--primary-color);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  height: 50px;
  justify-content: center;
  padding: 0 15px;
  border-radius: var(--border-radius);
  transition: background-color 0.3s, color 0.3s;
}

.button-59:hover {
  background-color: var(--highlight-color);
  color: var(--primary-bg-color);
}

.paste-button {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  margin-top: 1rem;
  background: var(--secondary-bg-color);
  color: var(--primary-color);
  border: none;
  border-radius: var(--border-radius);
  padding: 10px 20px;
  cursor: pointer;
  gap: 10px;
  box-shadow: 0 2px 5px var(--box-shadow-color);
  width: 235px;
}

.paste-button:hover {
  background: var(--highlight-color);
  color: var(--primary-bg-color);
}

@keyframes typing-effect {
  from {
    width: 0;
    visibility: hidden;
  }

  to {
    width: 100%;
    visibility: visible;
  }
}

.typing-animation {
  overflow: hidden;
  /* white-space: nowrap; */
  visibility: hidden;
  width: 0;
  display: block;
  animation: typing-effect 2.5s steps(40, end) forwards;
}

.result-label {
  font-family: 'Orbitron', sans-serif;
  font-weight: bold;
  font-size: 1.25rem;
  color: var(--primary-color);
  text-align: left;
  max-width: 100%;
  animation-delay: 0s;
  /* Starts immediately */
}

.result-explanation {
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  width: 100%;
  /* text-wrap: wrap; */
  /* Starts after label finishes */
}

.result-source {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.875rem;
  animation-delay: 1s;
  /* Starts after explanation finishes */
}

.result-display {
  width: 60%;
  margin-bottom: 10px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25);
  color: white;
  overflow-wrap: break-word;
  background-color: rgba(0, 128, 0, 0.8);
}

.hidden {
  display: none;
}

.result-display-true {
  background-color: rgba(0, 128, 0, 0.8);
}

.result-display-false {
  background-color: rgba(231, 76, 60, 0.8);
}

.result-display-partial {
  background-color: rgba(255, 165, 0, 0.8);
}

.search-container {
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 50.5%;
  border: 2px solid var(--secondary-color);
  border-radius: var(--border-radius);
  box-shadow: 0 4px 8px var(--box-shadow-color);
  margin-bottom: 1rem;
}

.search-box {
  z-index: 2;
  flex-grow: 1;
  padding: 10px 20px;
  background-color: transparent;
  color: var(--primary-color);
  border: none;
}

.search-icon-button {
  background: transparent;
  color: white;
  border: none;
  cursor: pointer;
}

.button-59-svg {
  margin-right: 3px;
  width: 24px;
  height: 24px;
  fill: #FFF;
  vertical-align: middle;
  transform: translateY(4px);
}

@media (max-width: 768px) {
  .get-extension {
    display: none;
  }

  .header-line {
    width: 100%;
  }

  .nav-buttons>.button-optional {
    display: none;
  }

  .nav-buttons {
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .button-59,
  .paste-button {
    flex-grow: 1;
    max-width: 100%;
    margin: 0 5px;
  }

  .search-container,
  .paste-button {
    width: 90%;
    max-width: none;
    order: 2;
  }
}


.Button-icons {
  margin-right: 3px;
}

#particles canvas {
  z-index: -1;
  height: 100%;
  width: 100%;
  position: absolute;
}

.button-59,
.paste-button,
.search-box {
  font-size: 1.125rem;
}

.imposter-behind-search {
  z-index: -100;
  height: 50px;
  width: 50px;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.imposter-behind-search {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  display: none;
  transform-origin: center;
  transition: transform var(--imposter-duration) linear;
}

:root {
  --imposter-duration: 30s;
  --left-start: -100px;
  --left-end: 120%;
  --top-start: 45%;
  --top-end: 45%;
  --size-start: 1;
  --size-end: 0.5;
  --angle-start: 0deg;
  --angle-end: 360deg;
}

/* Gets added to the amogus with JS */
.imposter-anim {
  display: block !important;
  animation-name: fall;
  animation-duration: var(--imposter-duration);
  animation-delay: 0s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}

@keyframes fall {
  0% {
    left: var(--left-start);
    top: var(--top-start);
    transform: rotate(var(--angle-start)) scale(var(--size-start));
  }

  100% {
    left: var(--left-end);
    top: var(--top-end);
    transform: rotate(var(--angle-end)) scale(var(--size-end));
  }
}

.link-no-underline {
  text-decoration: none;
}

.get-extension {
  text-decoration: none;
}

.Slogan {
  font-family: 'Orbitron', sans-serif;
  font-weight: bold;
  font-size: 1.25rem;
  color: var(--primary-color);
  text-align: center;
  width: 17rem;
  white-space: nowrap;
  overflow: hidden;
  animation: typing 2s steps(18),
    cursor 0.5s step-end infinite alternate;
  border-right: 5px solid;
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 17rem;
  }
}

@keyframes cursor {
  50% {
    border-color: transparent;
  }
}

.App {
  overflow-y: auto;
  height: 100vh;
}

.App::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.App::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.25);
}

/* Handle */
.App::-webkit-scrollbar-thumb {

  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
.App::-webkit-scrollbar-thumb:hover {
  background: #555;
}

