div.earth-body {
  padding: 0px;
  margin: 0px;
  overflow: hidden;
  height: 100%;
}
.webgl {
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1000;
}
.news-container {
  background-color: whitesmoke;
  max-width: 60%;
  min-width: 20%;
  height: 90%;
  max-height: 90%;
  margin-top: 5%;
  margin-left: 5%;
  padding: 2% 5%;
  z-index: 10000;
  border-radius: 13px;
}
.news-container * {
  overflow: scroll;
}
.news-table {
  width: 100%;
}
.article-url {
  text-align: right;
}
#close-container:hover {
  cursor: pointer
}
tr {
  padding-top: 8%;
  padding-bottom: 8%;
}
td {
  overflow: auto;
}

.animate-spin {
  animation: spin 5s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.progress-spinner {
  width: 60px;
  height: 60px;
}
.loader {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: -100px auto;
}

.circleFW,
.circleSW {
  width: 60px;
  height: 60px;
  transform-origin: center center;
  position: absolute;
  top: 80%;
  bottom: 0;
  left: 80%;
  right: 0;
  margin: auto;
}
.circleFW {
  animation: rotate 2s linear infinite;
}
.circleSW {
  animation: rotate-reverse 4s linear infinite;
}
.path,
.path2,
.path3,
.path4,
.path5,
.path6,
.path7,
.path8,
.path9 {
  stroke-linecap: square;
  stroke-width: 33;
  opacity: 0.8;
}

.path {
  stroke-dasharray: 1400, 2600;
  stroke-dashoffset: 0;
  animation: dash 3.7s ease-in-out infinite;
  opacity: 0.3;
}
.path2 {
  stroke-dasharray: 40, 2600;
  stroke-dashoffset: -500px;
}
.path3 {
  stroke-dasharray: 200, 2600;
  stroke-dashoffset: -700px;
}
.path4 {
  stroke-dasharray: 40, 2600;
  stroke-dashoffset: -2000px;
}
.path5 {
  stroke-dasharray: 100, 1600;
  stroke-dashoffset: 0;
}
.path6 {
  stroke-dasharray: 300, 2600;
  stroke-dashoffset: -1000px;
}
.path7 {
  stroke-dasharray: 40, 2600;
  stroke-dashoffset: -1200px;
}
.path8 {
  stroke-dasharray: 100, 2600;
  stroke-dashoffset: -300px;
}
.path9 {
  stroke-dashoffset: -300px;
  opacity: 0.8;
}
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotate-reverse {
  100% {
    transform: rotate(-360deg);
  }
}
@keyframes dash {
  0% {
    stroke-dasharray: 2000, 2600;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 2400, 2600;
    stroke-dashoffset: -500px;
  }
  100% {
    stroke-dasharray: 2000, 2600;
    stroke-dashoffset: 0;
  }
}
@keyframes dash2 {
  0% {
    stroke-dasharray: 100, 2600;
    stroke-dashoffset: -2000px;
  }
  50% {
    stroke-dasharray: 500, 2600;
    stroke-dashoffset: -1000px;
  }
  100% {
    stroke-dasharray: 100, 2600;
    stroke-dashoffset: -1960px;
  }
}
